.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.login-open {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.backdrop {
  background-color: var(--color-blue);
  width: 100vw;
  height: 100vh;
  padding-top: 127px;
  transition: opacity .8s ease-in-out, visibility .8s ease-in-out;
}

.login-modal {
  min-width: 320px;
  max-width: 335px;
  border: 2px solid var(--color-black);
  background-color: var(--color-light-gray-modal);
  border-radius: 18px;
  margin: 0 auto;
  padding: 40px 20px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .login-modal {
    max-width: 580px;
    max-height: 516px;
    padding: 80px 40px 40px;
  }
}

.login-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

.login-btn:hover, .login-btn:focus {
  outline: 2px solid var(--color-black);
  transform: rotate(360deg)scale3d(1.1, 1.1, 1);
}

@media screen and (min-width: 768px) {
  .login-btn {
    top: 24px;
    right: 24px;
  }

  .login-btn:hover, .login-btn:focus {
    outline: 3px solid var(--color-black);
    transform: rotate(360deg)scale3d(1.3, 1.3, 1);
  }
}

.login-btn-icon {
  stroke: var(--color-black);
  fill: none;
}

@media screen and (min-width: 768px) {
  .login-btn-icon {
    width: 28px;
    height: 28px;
  }
}

.login-field-container {
  flex-direction: column;
  gap: 24px;
  margin-bottom: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .login-field-container {
    gap: 28px;
  }
}

.login-field {
  position: relative;
}

.login-field-input {
  max-width: 295px;
  width: 100%;
  border: 2px solid var(--color-black);
  letter-spacing: .01em;
  text-transform: uppercase;
  background-color: var(--color-white);
  border-radius: 40px;
  padding: 16px 24px;
  font-family: DM Sans;
  font-weight: 700;
  line-height: 1.12;
}

.login-field-input:not(:placeholder-shown):required:valid {
  border: 2px solid #006400;
}

.login-field-input:not(:placeholder-shown):required:invalid {
  color: brown;
  border: 2px solid brown;
}

@media screen and (min-width: 768px) {
  .login-field-input {
    max-width: 500px;
    letter-spacing: -.01em;
    padding: 20px 40px;
    font-size: 18px;
    line-height: 1.33;
  }
}

.login-field-input:focus {
  border: 2px solid var(--color-blue);
  outline: none;
}

.login-field-input:focus ~ .login-field-icon {
  stroke: var(--color-blue);
}

.login-field-input::-ms-input-placeholder {
  color: var(--color-black);
}

.login-field-input::placeholder {
  color: var(--color-black);
}

#user_name {
  text-transform: none;
}

#user_name::-ms-input-placeholder {
  text-transform: uppercase;
}

#user_name::placeholder {
  text-transform: uppercase;
}

#login-field-name.is-hidden {
  position: absolute;
}

.modal-error-message {
  width: 100%;
  text-align: center;
  color: #006400;
  letter-spacing: -.3em;
  opacity: 0;
  font-size: 12px;
  transition: all 2s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(120%);
}

.login-field-input:not(:placeholder-shown):required:invalid ~ .modal-error-message {
  opacity: 1;
  letter-spacing: -.02em;
  color: brown;
  transition-delay: 1s;
}

.login-field-icon {
  stroke: var(--color-black);
  fill: rgba(0, 0, 0, 0);
  stroke: var(--color-black);
  pointer-events: none;
  position: absolute;
  top: 50%;
  right: 24px;
  transform: translateY(-50%);
}

.login-field-input:not(:placeholder-shown):required:valid ~ .login-field-icon {
  stroke: #006400;
}

.login-field-input:not(:placeholder-shown):required:invalid ~ .login-field-icon {
  stroke: #8b0000;
}

@media screen and (min-width: 768px) {
  .login-field-icon {
    width: 28px;
    height: 28px;
    right: 32px;
  }
}

.login-submit-btn {
  max-width: 295px;
  width: 100%;
  background: var(--color-black);
  color: var(--color-white);
  letter-spacing: .01em;
  text-transform: uppercase;
  border: 2px solid var(--color-black);
  border-radius: 40px;
  margin-bottom: 14px;
  padding: 16px;
  font-family: DM Sans;
  font-weight: 700;
  line-height: 1.12;
  transition: color .5s ease-in-out, background-color .5s ease-in-out;
}

.login-submit-btn:hover, .login-submit-btn:focus {
  background-color: var(--color-blue);
}

@media screen and (min-width: 768px) {
  .login-submit-btn {
    max-width: 500px;
    letter-spacing: -.01em;
    margin-bottom: 20px;
    padding: 20px;
    font-size: 18px;
  }
}

.login-sign {
  letter-spacing: .01em;
  text-transform: uppercase;
  justify-content: center;
  gap: 20px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
  display: flex;
}

@media screen and (min-width: 768px) {
  .login-sign {
    letter-spacing: -.01em;
    font-size: 18px;
    line-height: 1.33;
  }
}

.login-sign-up {
  color: var(--color-black-transparent);
  cursor: pointer;
  transition: all .5s ease-in-out;
}

.login-sign-up:hover, .login-sign-up:focus {
  color: var(--color-black);
}

.login-sign-in {
  color: var(--color-black-transparent);
  cursor: pointer;
  transition: all .5s ease-in-out;
}

.login-sign-in:hover, .login-sign-in:focus {
  color: rgba(17, 17, 17, .7);
}

.active {
  color: var(--color-blue);
  text-decoration: underline;
}

